import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store =new Vuex.Store({
    state:{
        logined:false,
        loginname:'',//登录名称
        accesstoken:'',//token
        loginedMoreMenus:{
          center:"个人中心",
          msg:"更多通知",
          logout:'登出'
        },
        unLoginedMoreMenus:{
          login:'登录'  
          // 字
        },
        isShowMoreMenus:false,
        moreMenus:{
          menu1:'login'
        },
        topics:[
          {name:'all',desc:'全部'},
          {name:'good',desc:'精品'},
          {name:'share',desc:'分享'},
          {name:'ask',desc:'回答'},
          {name:'job',desc:'招聘'},
          {name:'test',desc:'测试'},
        ]
    },
    getters:{  
        moreMen(state){
        // state表示当前store对象的state
        if(state.logined){
          return state.loginedMoreMenus
        }
        return state.unLoginedMoreMenus;
      },
    },
    mutations:{
        mutationsisShowMoreMenus:function(state,payload){
            // 将数值换成用户传入的值
            state.isShowMoreMenus=payload;
        },
        // 修改登录状态
        mutationIslogined:function(state){
          state.logined=true;
        },
        //修改登录名
        mutationsloginname:function(state,payload){
          state.loginname=payload;
        },
        // 修改token
        mutationstoken:function(state,payload){
          state.accesstoken=payload;
        },
        // 用户注销
        mutationslogout:function(state){
          state.logined=false;
          state.loginname='';
          state.accesstoken='';

        }
    },
    actions:{}
})
export default store;


