export default {
    name:'logout',
    created:function(){
      // 1、修改vuex状态数据
      this.$store.commit('mutationslogout');
      // 直接调用mut函数使用$store.commit
      // 2、清空本地存储数据
      window.localStorage.clear();
      // 3、页面跳转
      this.$router.push('/topiclist/all');
    }
}



